import { Children, FC, useRef } from 'react';

type ListProps = {
  defaultExpanded?: boolean;
  target: string;
}

const List: FC<ListProps> = (props) => {
  const { defaultExpanded, children, target } = props;
  const defaultExpandedRef = useRef(defaultExpanded);

  const kids = Children.map(children, (child) =>
    <li className={`nav-item flex-grow-0 my-1 mx-1`}>
      {child}
    </li>
  );

  const classTarget = target.startsWith('.') ? `${target} ` : '';
  const idTarget = target.startsWith('#') ? target : '';

  return (
    <div
      id={idTarget}
      className={`${classTarget}${defaultExpandedRef.current ? 'collapse-show' : 'collapse'} navbar-collapse py-lg-0 collapsible-nav`}
    >
      <ul className="ms-auto me-auto me-md-0 w-md-100 navbar-nav navbar-nav-hovers ms-md-2 flex-row me-md-auto">
        {kids}
      </ul>
    </div>
  );
};

export default List;
