import { useState, VFC } from 'react';
import Button from '../button';
import List from './list';
import Toggle from './toggle';

const Navigation: VFC = () => {
  const [isOpen, setIsOpen] = useState(false);

  const clickGithub = () => {
    window.open('https://github.com/cameronaziz', '_blank');
  };

  const toggleNavigation = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className="container position-sticky z-index-sticky top-0">
      <div className="row">
        <div className="col-12">
          <nav className="navbar navbar-expand-md blur border-radius-md top-0 z-index-fixed shadow position-absolute my-3 py-2 start-0 end-0 mx-4">
            <div className="container-fluid px-0">
              <Button
                label="Contact"
              />
              <Toggle
                target={'.collapsible-nav'}
                onClick={toggleNavigation}
                defaultExpanded
              />
              <List
                defaultExpanded>
                <Button
                  label="Blog"
                  color="secondary"
                />
                <Button
                  label="Experience"
                  color="secondary"
                />
              </List>
              <List defaultExpanded={defaultExpanded}>
                <Button
                  color="secondary"
                  icon="github"
                  onClick={clickGithub}
                />
                <Button
                  color="secondary"
                  icon="instagram"
                  onClick={clickGithub}
                />
                <Button
                  color="secondary"
                  icon="facebook"
                  onClick={clickGithub}
                />
                <Button
                  color="secondary"
                  icon="twitter"
                  onClick={clickGithub}
                />
              </List>
            </div>
          </nav>
        </div>
      </div>
    </div>
  );
};

export default Navigation;
